import { Component, OnInit } from '@angular/core';
import { SubscriptionService } from '../../../../../auth/_services/subscription.service';
import { QuickSidebarComponent } from '../../../../layouts/quick-sidebar/quick-sidebar.component';
import { SideBarToggleComponent } from '../../../../layouts/side-bar-toggle/side-bar-toggle.component';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DataService } from '../../../../../_services/data.service';
// import { NotificationsService } from 'angular4-notify';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from "@angular/router";

@Component({
    selector: 'app-subscription-list',
    templateUrl: "subscription-list.component.html",
    styles: []
})
export class SubscriptionListComponent implements OnInit {

    public subscriptionData = "";

    constructor(private imgBaseUrl: ScriptLoaderService, private router: Router, private toastr: ToastrService, private _subscriptionservice: SubscriptionService, private _dataService: DataService, private formBuilder: FormBuilder) { }


    ngOnInit() {


        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.subscription_list != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/


        this._subscriptionservice.getAll().subscribe(
            data => {
                this.subscriptionData = data.subscription;
                console.log('this.subscriptionData');
                console.log(this.subscriptionData);
            }
        );
    }


}
